import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service'
import { Router } from '@angular/router';
@Component({
  selector: 'app-details',
  templateUrl: './details.page.html',
  styleUrls: ['./details.page.scss'],
  providers: [HttpService]
})
export class DetailsPage implements OnInit {
  currentColor:any;
  currentColorButton:any;
  response:any;
  posts:any;
  constructor(public router: Router,public shared: HttpService) { 

    this.currentColor = 'light';
    this.currentColorButton='orangle';
    
  }

  ngOnInit() {
  }


  ionViewDidEnter() {
    if (navigator.onLine) {
            this.shared.ServiceCallGettingHere.ServiceCallRequestGettingHereData().then((res) => { 
              // debugger
              
              this.response=res;
              this.posts=this.response.content.posts;
              }, err => {
                
              })
    
          }else{
            this.shared.displayNetworkUpdate('No Internet Connection')
          }
        
      }




      NextPage(data){
        this.router.navigate(['/menu/first/tabs/tab1/details/details-next'], { state: { example: data } });
      }
  
}
